import axios from "axios";
import {
    GET_ERRORS,
    GET_CURRENT_PRICE
} from "./types";

import {SERVER_MAIN_URL} from '../config'

export const getCurrentPrice = () => dispatch => {
    axios
        .get(`${SERVER_MAIN_URL}/egaprice`)
        .then(res =>
            dispatch({
                type: GET_CURRENT_PRICE,
                payload: res,
            })
        ).catch(err =>
        dispatch({
            type: GET_ERRORS,
            payload: err.response.data
        })
    );
};
